<div class="page page-post"><div class="section section-header"><div class="pic" :style="bgcss(work.imgUrl.cover)"></div></div><div class="section section-intro"><div class="container"><div class="top"><div class="left"><div class="title">{{ work.title }}</div><div class="tags"><div class="tag tag-s" v-for="tag in work.tags">{{ tag }}</div></div></div><div class="right"><div class="brief"><p>{{ work.brief }}</p></div><div class="btns"><a class="btn btn-s btn-primary" v-if="work.urls.github_pages" :href="work.urls.github_pages" target="_blank"><span>前往網站</span></a><a class="btn btn-s btn-secondary" :href="work.urls.github" target="_blank"><span>GitHub</span></a></div></div></div><div class="bottom"><p class="description" v-if="work.description">{{ work.description}}</p><div class="video" v-if="work.video"><iframe :src="work.video" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen style="position:absolute;top:0;left:0;width:100%;height:100%;" title="twitter-search_demo.mp4"></iframe></div></div></div></div><div class="section section-photos"><div class="container"><div class="pic" v-for="pic in work.imgUrl.pics" v-html="getImg(pic)"></div></div></div></div>