<div class="page page-about"><section class="section section-intro h-screen-check"><div class="container"><div class="content"><h1>{{ intro.greeting }}</h1><p class="title" v-html="intro.brief"></p></div><div class="btns"><a class="btn btn-l btn-withIcon btn-primary" :href="resumeUrl" target="_blank">履歷<i class="fa-solid fa-arrow-up-right-from-square icon-s"></i></a><a class="btn btn-l btn-secondary" href="mailto:and535014@gmail.com">聯絡我！</a></div><div class="moreBtn" @click="goSkills"><p>更多</p><i class="fa-solid fa-angle-down"></i></div></div></section><section class="section section-skills" id="section-skills"><div class="container"><h2 class="title">相關專業技能</h2><ul class="skillList"><li v-for="skill in skillList"><h3 class="subtitle">{{ skill.title }}</h3><ul class="itemList"><li class="item" v-for="tag in skill.skills">{{ tag }}</li></ul></li></ul></div></section><section class="section section-experience"><div class="container"><h2 class="title">過往經歷</h2><ul class="exp-list"><li class="exp-item" v-for="(content,cid) in expList" :class="{ isShow: expDelta &gt; expListOffset[cid]}"><div class="dot"></div><div class="left"><div class="date"><div class="year subtitle">{{ content.date }}</div><div class="month subtitle">{{ content.month }}</div></div></div><div class="right"><div class="textArea"><div class="content"><h3 class="subtitle">{{ content.title }}</h3><p v-html="content.description"></p><ul v-if="content.list"><li v-for="list in content.list">{{ list }}</li></ul></div></div></div></li></ul></div></section></div>