<div class="page page-contact"><div class="section section-contact"><div class="container"><div class="wrapper"><div class="left"><img src="@/assets/email.gif"><h2 class="title">聯絡方式</h2><p class="description">謝謝你來到我的線上作品集，如果你有任何的問題或想法，歡迎寄信到下面信箱 ✉️ 或是留言 💬 告訴我喔!</p><ul class="info"><li><i class="fa-solid fa-location-dot"></i><span>Taipei, Taiwan</span></li><li><i class="fa-solid fa-envelope"></i><a class="hsiao-email" href="mailto:and535014@gmail.com">and535014@gmail.com</a></li></ul></div><div class="right"><h4 class="subtitle">有話想說？</h4><div class="form"><div class="name"><label> <span>姓名</span><input type="text" name="name" placeholder="請輸入你的名字" v-model="msg.name"></label></div><div class="email"><label> <span>信箱</span><input type="email" name="email" placeholder="請輸入你的信箱" v-model="msg.email"></label></div><div class="message"><label> <span>訊息</span><textarea name="message" placeholder="請輸入訊息" rows="3" v-model="msg.message"></textarea></label></div><div class="btn btn-primary btn-l" @click="updateMsg" v-if="!result">送出</div><transition name="fadeInBtn" mode="out-in"><div class="btn btn-primary btn-l btn-withIcon result" v-if="result === 'success'">您的訊息已送出<i class="fa-solid fa-circle-check"></i></div><div class="btn btn-primary btn-l btn-withIcon result error" v-else-if="result === 'error'">訊息送出失敗<i class="fa-solid fa-circle-xmark"></i></div></transition></div></div></div></div></div></div>