<div class="card card-container"><div class="pic-wrap" :style="bgcss(imgUrl.cover)"></div><div class="card info-card"><h2 class="title">{{ title }}</h2><p class="subtitle description">{{ brief }}</p><!--40字以內--><div class="tags"><div class="tag tag-s" v-for="tag in tags">{{ tag }}</div></div><div class="btns"> <router-link class="btn btn-s btn-primary" :to="moreInfo">更多資訊</router-link><a class="btn btn-s btn-secondary" v-if="urls.github_pages" :href="urls.github_pages" target="_blank">前往網站</a></div></div></div>